import { Component, OnInit, ViewEncapsulation } from '@angular/core';

//RXJS
import { BehaviorSubject } from 'rxjs'
//Service
import { UpgradeService } from './upgrade.service'
//Toastr
import { ToastrService } from 'ngx-toastr';
// NGRX
import { select, Store } from '@ngrx/store';
// State
import { AppState } from '../../../../core/reducers';
import { currentUser, Logout, AuthService, User } from '../../../../core/auth';

@Component({
  selector: 'kt-update',
  templateUrl: './update.component.html',
  styleUrls: ['./update.component.scss'],
  // encapsulation: ViewEncapsulation.None,
})
export class UpdateComponent implements OnInit {

  data = {
    update_activated: 0,
    update_key: "",
    update_signin: 0,
    update_url: "https://tacacsgui.com/api/tacacsgui/update/",
    version: ''
  }

  username = ''
  last_version = {
    description_brief: "",
    description_more: "",
    reinstall: 0,
    version: ""
  }

  outputMessage = ''
  outputMessage_ = new BehaviorSubject('')
  briefDescr_ = new BehaviorSubject('')
  moreDescr_ = new BehaviorSubject('')
  updateTrigger = new BehaviorSubject(false)
  manualTrigger = new BehaviorSubject(false)

  constructor(
    private service:UpgradeService,
    private toastr:ToastrService,
    private store: Store<AppState>,
    private auth: AuthService
  ) { }

  ngOnInit() {
    this.service.info().subscribe( data => {
      console.log(data)
      this.data = data.info
    })
  }

  change(){
    this.service.change({
      settings: 1,
      update_signin: +this.data.update_signin
    }).subscribe( data => {
      console.log(data)
      if (data.change_status) this.toastr.success('Changes Saved')
    })
  }

  copyKey(e){
    // console.log(e)
    if (e.isSuccess) this.toastr.success('You have copied update key')
  }

  checkUpdates(){
    this.outputMessage_.next('Loading...')
    this.briefDescr_.next('')
    this.moreDescr_.next('')
    this.service.update({version: this.data.version}).subscribe( data => {
      console.log(data)
      this.outputMessage = ''
      if (data.output){
        data.output.last_version.version = '0000'
        // data.output.last_version.reinstall = '1'
        if (data.output.error && data.output.error.message) {
          this.outputMessage_.next('Error: ' + data.output.error.message);
          return;
        }
        if (data.output.user)
          this.outputMessage += "Hello " + data.output.user.username + ",\n";
          // this.username = data.output.user.username;
        this.outputMessage += "Your api version: " + this.data.version + "\n";
        if (data.output.last_version) {
          this.outputMessage += "Last available version: " + data.output.last_version.version;
          this.briefDescr_.next('Brief Description: '+data.output.last_version.description_brief);
          this.moreDescr_.next('More Description: '+data.output.last_version.description_more);
          this.manualTrigger.next(!!+data.output.last_version.reinstall);
        }

        this.outputMessage_.next(this.outputMessage)
        if ( this.data.version != data.output.last_version.version){
          this.updateTrigger.next(true);
        }
      }
    })
  }

  upgrade(){
    this.service.upgrade().subscribe( data => {
      console.log(data)
      this.auth.logout().subscribe( data => {
  			console.log(data)
  		})
  		this.store.dispatch(new Logout());
    })
  }

}
