import { Injectable } from '@angular/core';
import { HttpClient, HttpParams, HttpEventType, HttpResponse, HttpRequest } from '@angular/common/http';
import { Observable, Subject } from 'rxjs';
import { map, first } from 'rxjs/operators';

const API_URL = 'api/backup/';

@Injectable({
  providedIn: 'root'
})
export class BackupService {

  constructor(private http: HttpClient) {}

  settings() {
    return this.http.get<any>(API_URL + 'settings/')
      .pipe(
        first(),
        map( (data) => {
          return data
        } )
      )
  }

  settings_(params: any) {
    return this.http.post<any>(API_URL + 'settings/', params)
      .pipe(
        first(),
        map( (data) => {
          return data
        } )
      )
  }

  postFile(fileToUpload: File, settings: any) {

    const formData: FormData = new FormData();
    formData.append('tcfg', fileToUpload, fileToUpload.name);
    // formData.append('action', settings.action);
    // formData.append('name', settings.name);
    return this.http
      .post<any>(API_URL + 'upload/', formData).pipe(
        map(() => { return true; })
      )
    }

    upload(file: File) {
      const formData = new FormData();
      formData.append('file', file);
      var config = {
          headers: {'Content-Type': undefined},
          transformRequest: []
      };
      return this.http.post<any>(API_URL + 'upload/', formData, config).pipe(map((event) => {

        switch (event.type) {

          case HttpEventType.UploadProgress:
            const progress = Math.round(100 * event.loaded / event.total);
            return { status: 'progress', message: progress };

          case HttpEventType.Response:
            return event.body;
          default:
            return `Unhandled event: ${event.type}`;
        }
      })
      );

    }

  make(settings:{}) {
    return this.http.post<any>(API_URL + 'make/', settings)
      .pipe(
        first(),
        map( (data) => {
          return data
        } )
      )
  }

  del(settings:{}) {
    return this.http.post<any>(API_URL + 'delete/', settings)
      .pipe(
        first(),
        map( (data) => {
          return data
        } )
      )
  }

  restore(settings:{}) {
    // name: 2019-05-23_tcfg_393.sql
    // type: tcfg
    return this.http.post<any>(API_URL + 'restore/', settings)
      .pipe(
        first(),
        map( (data) => {
          return data
        } )
      )
  }

}
