import { Injectable } from '@angular/core';
import { HttpClient, HttpParams } from '@angular/common/http';
import { map, first } from 'rxjs/operators';

const API_URL = 'api/update/';

@Injectable({
  providedIn: 'root'
})
export class UpgradeService {

  constructor(private http: HttpClient) {}

  info() {
    return this.http.get<any>(API_URL + 'info/',)
      .pipe(
        first(),
        map( (data) => {
          return data
        } )
      )
  }
  change(settings:{}) {
    // settings: 1
    // update_signin: 0
    return this.http.post<any>(API_URL + 'change/', settings)
      .pipe(
        first(),
        map( (data) => {
          return data
        } )
      )
  }
  update(settings:{}) {
    // version: 1
    return this.http.post<any>(API_URL, settings)
      .pipe(
        first(),
        map( (data) => {
          return data
        } )
      )
  }

  upgrade(){
    return this.http.post<any>(API_URL + 'upgrade/', {})
      .pipe(
        first(),
        map( (data) => {
          return data
        } )
      )
  }
}
